	module.exports = {"data":{"markdownRemark":{"html":"<p>原付でお薦めできる車種についてまずエンジンが2stか4stかを決めなきゃな。</p>\n<ul>\n<li>2st(2サイクル)の特徴：パワー・最高速共に優れる、独特の高音、燃費悪い、オイル補給しなきゃいけない、排ガス汚い、シンプルなエンジンの中身</li>\n<li>4st(4サイクル)の特徴：パワー・最高速共に(特に原付は致命的に)弱い、重低音、燃費良い、排ガス綺麗、エンジンの中身複雑\n2stはそろそろ絶滅傾向にあるから(もう新品では2stは買えないと思う)俺としては今のうち2stのヤツ買っておくのが手だと思うんだが、どうだ？ ちなみに燃費は25kmの往復50kmを原付で走ったとして2stでは1.5L、4stなら0.5L位の消費だろうな。(かなりテキトーだが、よほど変な走りかたしなきゃそんなもんだろう)値段はハイオクでも1L130円弱。これ考えると電車はボッタにしか思えなくなるぜ。 次に選ぶのはスクーターか、ミッションタイプかどっちにするか、だ。スクーターはすぐ慣れるから最初楽しいがすぐ飽きる。ミッションは慣れるまでかなり時間かかるが、慣れれば壊れるまでトコトン楽しめる。あとミッションはスクーターよりいじくりやすいのも利点かな。 最後に買うなら中古だ。新品で原付はバカらしいぜ。ただネットオークションだけは信用ならんから避けておけ。</li>\n</ul>","frontmatter":{"cover":null,"date":"2004-11-01T00:00:00+09:00","strDate":"2004/10/31","title":"バイクの選び方 1"}}},"pathContext":{"path":"/ja/blog/2004/11/01-バイクの選び方-1/","langKey":"ja"}}

